/* roboto-condensed-regular - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* roboto-condensed-700 - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* pt-mono-regular - latin_cyrillic-ext */
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: local(" "), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
body {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  margin: 0;
  padding: 0;
  color: #666666;
  background-color: #ffffff;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.page-main {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  position: absolute;
  width: 100%;
  top: 33px;
  padding: 0 30px 0;
}
